.header-container {
  display: flex;
  flex-direction: column;
  background-color: #f9f9f9;
  position: relative;

  @media (min-width: $tablet-width) {
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 24px;
      background-image: url("../img/decor/big-wave.svg");
      background-repeat: repeat-x;
    }
  }

  @media (min-width: $desktop-width) {
    &::after {
      height: 34px;
      background-image: url("../img/decor/big-wave-desktop.svg");
    }
  }
}

.header-container__wrapper {
  @media (min-width: $tablet-width) {
    margin: 0 auto;
    width: 668px;
  }

  @media (min-width: $desktop-width) {
    width: 1050px;
  }
}

.header-container__hero {
  height: 283px;
  background-image: url("../img/hero-mobile@2x 1.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-color: #f9f9f9;

  @media (min-width: $tablet-width) {
    height: 626px;
    background-size: cover;
  }
}

.header-container__title {
  text-align: center;
  margin: 0 auto;
  padding: 0;
  padding-top: 73px;
  width: 80%;

  @media (min-width: $tablet-width) {
    font-size: 43px;
    line-height: 50px;
    padding-top: 155px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 155px;
    font-size: 53px;
    line-height: 57px;
  }
}

.header-container__menu-buttons {
  display: flex;
  flex-direction: column;
  position: relative;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.header-container__menu-link {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  min-height: 103px;
  padding: 0 30px;
  align-items: center;
  color: $menu-button-font-color;
  font-size: 19px;
  font-weight: 700;
  line-height: 24px;
  text-align: left;
  text-decoration: none;
  position: relative;
  fill: $additional-menu-button-decor-color;

  &:hover {
    fill: $additional-menu-button-decor-hover-color;
    transform: translateY(-5px);
  }

  &:active {
    fill: $additional-menu-button-decor-active-color;
  }

  &:last-child {
    margin-bottom: 31px;
  }

  &--option-one {
    background-color: $menu-button-color;
  }

  &--option-one:hover {
    background-color: $menu-button-hover-color;
    box-shadow: 0 5px 0 0 $menu-button-color;
  }

  &--option-one:active {
    background-color: $menu-button-active-color;
  }

  &--option-two {
    background-color: $additional-menu-button-color;
  }

  &--option-two:hover {
    background-color: $additional-menu-button-hover-color;
    box-shadow: 0 5px 0 0 $additional-menu-button-hover-color;
  }

  &--option-two:active {
    background-color: $additional-menu-button-hover-color;
  }

  @media (min-width: $tablet-width) {
    width: 100%;
    min-height: 113px;
    padding: 0 36px 19px;
    font-size: 20px;
    line-height: 30px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 0 36px 22px;
  }
}

.header-container__menu-icon {
  transform: translateY(5px);
}
